.section {
  padding-bottom: 60px;
}

.header {
  background-color: #ffb8ca;
}

.header__mobile {
  padding-top: 8px;
  padding-bottom: 5px;
  display: flex;
}

.header__menu {
  width: 100%;
  height: 100%;
  z-index: 5;
  background-color: #e17992;
  flex-direction: column;
  padding-top: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.header__menu.is-open {
  transform: translateX(0%);
}

.menu-button {
  cursor: pointer;
  background: none;
  border: none;
  margin-left: auto;
}

.menu-button__close {
  margin-bottom: 26px;
}

.menu-button__burger {
  fill: #fff;
  stroke: #fff;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1), stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.menu-button__burger:hover, .menu-button__burger:focus {
  fill: #d41443;
  stroke: #d41443;
}

.menu-button__buy {
  text-align: center;
  width: 209px;
  height: 44px;
  color: #d41443;
  background-color: #fff;
  border: none;
  border-radius: 22px;
  align-items: center;
  margin-bottom: 19px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.nav__list {
  border-top: 1px solid #e18298;
  margin-left: -20px;
  margin-right: -20px;
  padding-left: 20px;
  padding-right: 20px;
}

.nav__list:last-child {
  border-bottom: 1px solid #e18298;
}

.nav__link {
  color: #fff;
  padding-top: 11px;
  padding-bottom: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.nav__link:hover, .nav__link:focus, .nav__link--selection {
  color: #d41443;
}

.hero {
  height: 439px;
  background-color: #ffb8ca;
  padding-top: 20px;
  overflow: hidden;
}

.hero__container {
  margin-top: -20px;
  padding-top: 131px;
  position: relative;
}

.hero__circle {
  width: 346px;
  height: 344px;
  background-color: #ffa5ba;
  border-radius: 50%;
  position: absolute;
  top: 20px;
  left: 11.25%;
  overflow: visible;
}

.hero__image {
  position: absolute;
  top: 9px;
  left: 81px;
}

.hero__space {
  z-index: 2;
  position: relative;
}

.hero__title {
  letter-spacing: .04em;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 20px;
  font-family: Titan One, cursive;
  font-size: 26px;
  font-weight: 400;
  line-height: 1.15;
}

.hero__title--accent {
  color: #d41443;
  font-size: 48px;
